import { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import contactImg from "../assets/img/contact-img.svg";
// import 'animate.css';
import nav1 from "../assets/nav-icon1.svg";
import nav2 from "../assets/nav-icon2.svg";

import github from "../assets/img/github.png";
import email from "../assets/img/email.png";
import TrackVisibility from "react-on-screen";

export const Contact = () => {
	return (
		<section className="contact" id="connect">
			<Container>
				<Row className="align-items-center">
					<Col size={12} md={6}>
						<TrackVisibility>
							{({ isVisible }) => (
								<img className={isVisible ? "animate__animated animate__zoomIn" : ""} src={contactImg} alt="Contact Us" />
							)}
						</TrackVisibility>
					</Col>
					<Col size={12} md={6}>
						<h2>Don't Hesitate To Get In Touch</h2>
						<span className="navbar-text align-items-center">
							<div className="social-icon">
								<a href="https://www.linkedin.com/in/rheann-sequeira">
									<img src={nav1} alt="linkedin" />
								</a>
								<a href="mailto:rheann.seq@gmail.com">
									<img src={email} alt="email" />
								</a>
								<a href="https://github.com/rheann-seq">
									<img src={github} alt="github" />
								</a>
							</div>
						</span>
					</Col>
				</Row>
			</Container>
		</section>
	);
};

